.image_wrapper {
    display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      height: 250px;
     align-items: flex-start;
     justify-content: space-between;
   max-width: 100%;
   
  }
  .checked {
    color: orange;
  }
  .image_wrapper > div {
  width: 28%;
  border-radius: 10px;
  border-color: rgb(183, 147, 93);
  border-width: 3px;
  border-style:solid;

  background-color: antiquewhite;
  }

  img {
    width: 100%;
  height: 100%;
  }

  button 
  {
    width: 100px;
  }
